import * as React from 'react'
import * as ReactDOM from 'react-dom'
import * as PropTypes from 'prop-types'

import Menu from './menu/Menu'
import MenuItem from './menu/MenuItem'
import SubMenu from './menu/SubMenu'
import CodeBox from 'src/components/codebox/CodeBox'
import Button from './components/button/Button'

const componentName = 'MenuPage'

export default () => {
  return (<div>
    <section>
      <h1>Menu </h1>
      <p className="text">
        
      </p>
    </section>
    <section>
      <h2></h2>
      <p className="text">
        </p>
    </section>
    <section>
      <h2></h2>
    </section>
    <div className="example" style={{ display: "flex", flexDirection: "column" }}>
      <div className="container">
        <Menu mode="horizontal">
          <MenuItem key="item1">Option 1</MenuItem>
          <MenuItem key="item2">Option 2</MenuItem>
          <MenuItem key="item3">Option 3</MenuItem>
          <SubMenu key="sub" title="SubMenu">
            <MenuItem key="item4">Option 4</MenuItem>
            <MenuItem key="item5">Option 5</MenuItem>
            <MenuItem key="item6">Option 6</MenuItem>
          </SubMenu>
        </Menu>
      </div>

      <CodeBox
        title=""
        description=""
      >
        {''}
      </CodeBox>
    </div>
    <div className="example" style={{ display: "flex", flexDirection: "column" }}>
      <div className="container">
        <Menu mode="vertical" theme="dark">
          <MenuItem key="item1">Option 1</MenuItem>
          <MenuItem key="item2">Option 2</MenuItem>
          <MenuItem key="item3">Option 3</MenuItem>
          <SubMenu key="sub" title="SubMenu">
            <MenuItem key="item4">Option 4</MenuItem>
            <MenuItem key="item5">Option 5</MenuItem>
            <MenuItem key="item6">Option 6</MenuItem>
          </SubMenu>
        </Menu>
      </div>

      <CodeBox
        title=""
        description=""
      >
        {''}
      </CodeBox>
    </div>

    <section>
      <h2>API</h2>
    </section>
    <table className="api-table">
      <thead>
        <tr>
          <th></th>
          <th className="des"></th>
          <th className="type"></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>mode	
</td>
          <td className="des">	</td>
          <td className="type">	'vertical' | 'horizontal'</td>
          <td>'vertical'</td>
        </tr>
        <tr>
          <td>theme	</td>
          <td className="des">	</td>
          <td className="type">'light' | 'dark'</td>
          <td>	'light'</td>
        </tr>
      </tbody>
    </table>
  </div>
  )
}




