import * as React from 'react'

import CodeBox from '../components/codebox/CodeBox'
import Button from './components/button/Button'
import Popover from './components/popover/Popover'


const content = (
    <div style={{ width: "150px", padding: "5px 20px" }}>
        <div style={{
            fontSize: "16px",
            lineHeight: " 24px",
            color: " rgba(0, 0, 0, 0.65)",
            boxShadow: " 0 4px 4px -4px rgb(0 0 0 / 10%)",
            padding: " 5px 20px",
        }}>Title</div>
        <ul className="content-list"><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li></ul>
    </div>
)
const componentName = 'ButtonPage'
export default () => {
    return (
        <div>
            <section>
                <h1>Popover </h1>
                <p className="text"></p>
            </section>
            <section>
                <h2></h2>
                <p className="text"></p>
            </section>
            <section>
                <h2></h2>
            </section>
            <div className="example">
                <div className="container">
                    <Popover trigger="hover" content={content} position="top"><Button type="primary">hover</Button></Popover>
                    <span>{" "}</span>
                    <Popover trigger="click" content={content} position="top"><Button type="primary">click</Button></Popover>
                </div>

                <CodeBox
                    title=""
                    description=" hover, click"
                >

                    {''}
                </CodeBox>
            </div>

            <section>
                <h2>API</h2>
            </section>
            <table className="api-table">
                <thead>
                    <tr>
                        <th></th>
                        <th className="des"></th>
                        <th className="type"></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>content</td>
                        <td className="des"></td>
                        <td className="type">string | React.ReactNode</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>trigger</td>
                        <td className="des"></td>
                        <td className="type">'hover' | 'click'</td>
                        <td>'hover'</td>
                    </tr>
                </tbody>
            </table>
            {/* <section>
        <h2>Method</h2>
      </section>
      <table className="method-table">
        <thead>
          <tr>
            <th></th>
            <th className="des"></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>focus()</td>
            <td className="des"></td>
            <td className="params"></td>
          </tr>
          <tr>
            <td>blur()</td>
            <td className="des"></td>
            <td className="params"></td>
          </tr>
        </tbody>
      </table> */}
        </div>
    )
}



