import * as React from 'react'

import CodeBox from '../components/codebox/CodeBox'
import Button from './components/button/Button'

const componentName = 'ButtonPage'
export default () => {
  return (
    <div>
      <section>
        <h1>Button </h1>
        <p className="text"></p>
      </section>
      <section>
        <h2></h2>
        <p className="text"></p>
      </section>
      <section>
        <h2></h2>
      </section>
      <div className="example" style={{display:"flex",flexDirection:"column"}}>
        <div className="container">
          <Button type="primary">Primary</Button><span>{" "}</span>
          <Button type="dashed">dashed</Button><span>{" "}</span>
          <Button type="default">default</Button><span>{" "}</span>
          <Button type="danger">danger</Button><span>{" "}</span>
          <Button disabled={true} size="medium">disabled</Button>
        </div>

        <CodeBox
          title=""
          description=""
        >

          {''}
        </CodeBox>
      </div>

      <section>
        <h2>API</h2>
      </section>
      <table className="api-table">
        <thead>
          <tr>
            <th></th>
            <th className="des"></th>
            <th className="type"></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>type</td>
            <td className="des"></td>
            <td className="type">'default' | 'dashed' | 'primary' | 'danger'</td>
            <td>'default'</td>
          </tr>
          <tr>
            <td>size</td>
            <td className="des"></td>
            <td className="type">'small' | 'medium' | 'large'</td>
            <td>'medium'</td>
          </tr>
          <tr>
            <td>disabled</td>
            <td className="des"></td>
            <td className="type">boolean</td>
            <td>false</td>
          </tr>
        </tbody>
      </table>
      {/* <section>
        <h2>Method</h2>
      </section>
      <table className="method-table">
        <thead>
          <tr>
            <th></th>
            <th className="des"></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>focus()</td>
            <td className="des"></td>
            <td className="params"></td>
          </tr>
          <tr>
            <td>blur()</td>
            <td className="des"></td>
            <td className="params"></td>
          </tr>
        </tbody>
      </table> */}
    </div>
  )
}



